"use client";

import { useEffect, useState } from "react";
import { useSocket } from "@/hooks/useSocket";
import {
  Heart,
  AlertCircle,
  Users,
  Droplet,
  TrendingDown,
  LogOut,
} from "lucide-react";
import Link from "next/link";

// ðŸ‘‰ FIXED MAP (dynamic import prevents SSR errors)
import dynamic from "next/dynamic";
const HospitalMap = dynamic(() => import("@/components/HospitalMap"), {
  ssr: false,
});

// Existing components (unchanged)
import BloodCompatibilityChart from "@/components/blood-compatibility-chart";
import AIChatbot from "@/components/ai-chatbot";
import ProfileMenu from "@/components/profile-menu";
// Correct URLs (backend runs on 5000)
const STATS_URL = "http://localhost:5000/hospital/stats";
const ACTIVE_DONORS_URL = "http://localhost:5000/hospital/active-donors";
const REQUESTS_URL = "http://localhost:5000/request/all";


export default function HospitalDashboard() {
  const [hospitalName, setHospitalName] = useState("Hospital");
  const [hospitalId, setHospitalId] = useState<string | null>(null);
  const [hospitalEmail, setHospitalEmail] = useState("");
  const [hospitalPhone, setHospitalPhone] = useState("");
  const [showProfileMenu, setShowProfileMenu] = useState(false);
  const [requests, setRequests] = useState<any[]>([]);
  const [hospitalRequests, setHospitalRequests] = useState<any[]>([]);
  const [recipientRequests, setRecipientRequests] = useState<any[]>([]);
  const [activeDonors, setActiveDonors] = useState<any[]>([]);
  const [stats, setStats] = useState({
    totalRequests: 0,
    livesSaved: 0,
    donorsNearby: 0,
    avgMatchTime: 0,
  });

  const socketRef = useSocket();

  const [isRefreshing, setIsRefreshing] = useState(false);

  /* -----------------------------------------------
     REFRESH ALL DATA
  -----------------------------------------------*/
  const handleRefresh = async () => {
    setIsRefreshing(true);
    try {
      await Promise.all([
        (async () => {
          const res = await fetch(STATS_URL);
          const data = await res.json();
          setStats({
            totalRequests: data.totalRequests || 0,
            livesSaved: data.livesSaved || 0,
            donorsNearby: data.donorsNearby || 0,
            avgMatchTime: data.avgMatchTime || 0,
          });
        })(),
        (async () => {
          const res = await fetch(ACTIVE_DONORS_URL);
          const data = await res.json();
          setActiveDonors(data.activeDonors || []);
        })(),
        (async () => {
          const res = await fetch(REQUESTS_URL);
          const data = await res.json();
          const allRequests = data.requests || [];
          setRequests(allRequests);
          setHospitalRequests(allRequests.filter((r: any) => r.hospital));
          setRecipientRequests(allRequests.filter((r: any) => !r.hospital));
        })(),
      ]);
    } catch (err) {
      console.error("Refresh error:", err);
    } finally {
      setIsRefreshing(false);
    }
  };

  /* ----------------------------------------------
     LOAD HOSPITAL NAME
  -----------------------------------------------*/
  useEffect(() => {
    const stored = localStorage.getItem("user");
    if (stored) {
      const user = JSON.parse(stored);
      setHospitalName(user.fullName || "Hospital");
      setHospitalId(user._id || null);
      setHospitalEmail(user.email || "");
      setHospitalPhone(user.phone || "");
    }
  }, []);

  /* ----------------------------------------------
     FETCH STATS
  -----------------------------------------------*/
  const loadStats = async () => {
    try {
      const res = await fetch(STATS_URL);
      const data = await res.json();
      if (data.success) {
        setStats((prev) => ({
          ...prev,
          donorsNearby: data.donorsNearby,
          avgMatchTime: data.avgMatchTime,
        }));
      }
    } catch (err) {
      console.error(err);
    }
  };

  /* ----------------------------------------------
     FETCH ACTIVE DONORS
  -----------------------------------------------*/
  const loadActiveDonors = async () => {
    try {
      const res = await fetch(ACTIVE_DONORS_URL);
      const data = await res.json();
      if (data.success) {
        setActiveDonors(data.donors || []);
      }
    } catch (err) {
      console.error(err);
    }
  };

  /* ----------------------------------------------
     FETCH REQUESTS
  -----------------------------------------------*/
  const loadRequests = async () => {
    try {
    const res = await fetch(REQUESTS_URL);

      const data = await res.json();

      const reqList = data.requests || [];
      setRequests(reqList);

      // Split requests: hospital-made vs recipient-made
      const hospitals = reqList.filter((r: any) => r.hospital && r.hospital !== "");
      const recipients = reqList.filter((r: any) => !r.hospital || r.hospital === "");
      setHospitalRequests(hospitals);
      setRecipientRequests(recipients);

      setStats((prev) => ({
        ...prev,
        totalRequests: reqList.length,
        livesSaved: Math.floor(reqList.length * 1.5),
      }));
    } catch (err) {
      console.error(err);
    }
  };

  /* ----------------------------------------------
     INITIAL LOAD + SOCKET LISTENER
  -----------------------------------------------*/
  useEffect(() => {
    loadStats();
    loadActiveDonors();
    loadRequests();

    const socket = socketRef.current;
    if (!socket) return;

    // Listen for donor availability changes
    socket.on("donor_status_changed", () => {
      loadStats();
      loadActiveDonors();
    });

    // Listen for new requests created by this hospital or other hospitals
    socket.on("request_created", (payload: any) => {
      setRequests((prev) => [payload, ...prev]);
      setStats((prev) => ({
        ...prev,
        totalRequests: prev.totalRequests + 1,
      }));
      console.log("New request created (real-time):", payload);
    });

    // Listen for donor availability changes to refresh donor list
    socket.on("availability_changed", () => {
      loadActiveDonors();
      loadStats();
    });
    // Listen for request fulfilled (when hospital marks donation completed)
    socket.on("request_fulfilled", (payload: any) => {
      // Remove fulfilled request from active requests
      setHospitalRequests((prev) => prev.filter((r: any) => r._id !== payload.requestId));
      setRecipientRequests((prev) => prev.filter((r: any) => r._id !== payload.requestId));
      setRequests((prev) => prev.filter((r: any) => r._id !== payload.requestId));

      // Update stats
      setStats((prev) => ({
        ...prev,
        totalRequests: Math.max(0, prev.totalRequests - 1),
      }));
    });

    return () => {
      socket.off("donor_status_changed");
      socket.off("request_created");
      socket.off("availability_changed");
      socket.off("request_fulfilled");
    };
  }, [socketRef]);

  /* ----------------------------------------------
     UI HELPERS
  -----------------------------------------------*/
  const getUrgencyColor = (urg: string) => {
    switch (urg?.toUpperCase()) {
      case "CRITICAL":
        return "bg-red-600 text-white";
      case "HIGH":
        return "bg-orange-500 text-white";
      case "MODERATE":
        return "bg-yellow-500 text-white";
      default:
        return "bg-green-500 text-white";
    }
  };

  const getStatusColor = (status: string) =>
    status === "Pending"
      ? "bg-yellow-100 text-yellow-700"
      : status === "Fulfilled"
      ? "bg-green-100 text-green-700"
      : "bg-gray-300 text-gray-700";

  /* ----------------------------------------------
     RENDER UI
  -----------------------------------------------*/
  return (
    <main className="min-h-screen bg-gradient-to-br from-white via-red-50 to-white">
      {/* HEADER */}
      <header className="bg-gradient-to-r from-red-600 to-red-700 text-white p-6 sticky top-0 z-30 shadow-lg">
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <div className="flex items-center gap-3">
            <Heart className="w-8 h-8 fill-white" />
            <h1 className="text-3xl font-bold">{hospitalName} Dashboard</h1>
          </div>

          <div className="flex items-center gap-3">
            <button
              onClick={handleRefresh}
              disabled={isRefreshing}
              className="p-2 rounded-lg hover:bg-red-500 transition disabled:opacity-50 disabled:cursor-not-allowed"
              title="Refresh data"
            >
              ðŸ”„
            </button>
            <ProfileMenu />
            <Link
              href="/"
              className="p-2 rounded-lg hover:bg-red-500 transition"
              onClick={() => localStorage.clear()}
            >
              <LogOut className="w-6 h-6" />
            </Link>
          </div>
        </div>
      </header>

      {/* MAIN BODY */}
      <div className="max-w-7xl mx-auto p-6 space-y-12">
        {/* TOP CARD */}
        <div className="bg-white rounded-xl shadow-lg overflow-hidden border-l-4 border-red-600">
          <div className="grid md:grid-cols-2 gap-6">
            <div className="p-8">
              <h2 className="text-3xl font-bold text-red-700">{hospitalName}</h2>
              <p className="text-gray-600">Real-time donor coordination system</p>

              <div className="mt-4 space-y-1 text-gray-800">
                <p>
                  Active Requests: <b>{stats.totalRequests}</b>
                </p>
                <p>
                  Lives Saved: <b>{stats.livesSaved}</b>
                </p>
              </div>
            </div>

            <div className="h-64 bg-gradient-to-br from-red-500 to-red-600 relative overflow-hidden">
              <img
                src="/modern-hospital-medical-emergency-healthcare.jpg"
                alt="hospital"
                className="w-full h-full object-cover"
              />
              <div className="absolute inset-0 bg-black/20 flex items-center justify-center">
                <Heart className="w-20 h-20 text-white opacity-80" />
              </div>
            </div>
          </div>
        </div>

        {/* MAP + STATS */}
        <div className="bg-white p-6 rounded-xl shadow-lg">
          <div className="flex justify-between items-center mb-4">
            <h3 className="text-xl font-bold">
              Active Donors Nearby: {stats.donorsNearby}
            </h3>
            <Link
              href="/hospital/new-request"
              className="bg-red-600 hover:bg-red-700 text-white px-6 py-2 rounded-lg font-semibold transition"
            >
              + New Request
            </Link>
          </div>

          <div className="w-full relative overflow-hidden h-96">
            <HospitalMap activeDonors={activeDonors} />
          </div>
        </div>

        {/* LARGE STAT CARDS */}
        <div className="grid md:grid-cols-4 gap-6">
          <StatCard icon={Users} label="Donors Nearby" value={stats.donorsNearby} />
          <StatCard icon={Droplet} label="Avg Match Time" value={`${stats.avgMatchTime} min`} />
          <StatCard icon={TrendingDown} label="Lives Saved" value={stats.livesSaved} />
          <StatCard icon={Heart} label="Active Requests" value={stats.totalRequests} />
        </div>

        {/* BLOOD COMPATIBILITY */}
        <BloodCompatibilityChart />

        {/* REQUEST TABLES */}
        <div className="space-y-8">
          {/* Hospital-created requests */}
          <div className="bg-white rounded-xl shadow-lg p-8 border-2 border-red-200">
            <h2 className="text-2xl font-bold mb-4">Hospital-Created Requests</h2>

            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b">
                    <th className="py-3 px-4 text-left">Hospital</th>
                    <th className="py-3 px-4 text-left">Blood Type</th>
                    <th className="py-3 px-4 text-left">Units</th>
                    <th className="py-3 px-4 text-left">Urgency</th>
                    <th className="py-3 px-4 text-left">Status</th>
                    <th className="py-3 px-4 text-left">Confirmation</th>
                  </tr>
                </thead>

                <tbody>
                  {(hospitalRequests || []).map((req: any, i: number) => (
                    <tr key={`h-${i}`} className="border-b hover:bg-red-50">
                      <td className="py-4 px-4">{req.hospital || "N/A"}</td>
                      <td className="py-4 px-4">{req.bloodType}</td>
                      <td className="py-4 px-4">{req.unitsNeeded}</td>
                      <td className="py-4 px-4">
                        <span
                          className={`px-3 py-1 rounded-full text-xs ${getUrgencyColor(
                            req.urgency,
                          )}`}
                        >
                          {req.urgency}
                        </span>
                      </td>
                      <td className="py-4 px-4">
                        <span
                          className={`px-3 py-1 rounded-full text-xs ${getStatusColor(
                            req.status,
                          )}`}
                        >
                          {req.status}
                        </span>
                      </td>
                      <td className="py-4 px-4">
                        <span className={`px-3 py-1 rounded-full text-xs ${
                          req.confirmationStatus === "Confirmed" ? "bg-green-100 text-green-700" :
                          req.confirmationStatus === "Rejected" ? "bg-red-100 text-red-700" :
                          "bg-gray-100 text-gray-700"
                        }`}>
                          {req.confirmationStatus || "Pending"}
                        </span>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>

          {/* Recipient-created requests */}
          <div className="bg-white rounded-xl shadow-lg p-8 border-2 border-rose-200">
            <h2 className="text-2xl font-bold mb-4">Recipient-Created Requests</h2>

            <div className="overflow-x-auto">
              <table className="w-full">
                <thead>
                  <tr className="border-b">
                    <th className="py-3 px-4 text-left">Recipient</th>
                    <th className="py-3 px-4 text-left">Blood Type</th>
                    <th className="py-3 px-4 text-left">Units</th>
                    <th className="py-3 px-4 text-left">Urgency</th>
                    <th className="py-3 px-4 text-left">Status</th>
                    <th className="py-3 px-4 text-left">Confirmation</th>
                  </tr>
                </thead>

                <tbody>
                  {(recipientRequests || []).map((req: any, i: number) => (
                    <tr key={`r-${i}`} className="border-b hover:bg-rose-50">
                      <td className="py-4 px-4">{req.requestedByName || req.requesterName || req.requestedBy || "N/A"}</td>
                      <td className="py-4 px-4">{req.bloodType}</td>
                      <td className="py-4 px-4">{req.unitsNeeded}</td>
                      <td className="py-4 px-4">
                        <span
                          className={`px-3 py-1 rounded-full text-xs ${getUrgencyColor(
                            req.urgency,
                          )}`}
                        >
                          {req.urgency}
                        </span>
                      </td>
                      <td className="py-4 px-4">
                        <span
                          className={`px-3 py-1 rounded-full text-xs ${getStatusColor(
                            req.status,
                          )}`}
                        >
                          {req.status}
                        </span>
                      </td>
                      <td className="py-4 px-4">
                        <span className={`px-3 py-1 rounded-full text-xs ${
                          req.confirmationStatus === "Confirmed" ? "bg-green-100 text-green-700" :
                          req.confirmationStatus === "Rejected" ? "bg-red-100 text-red-700" :
                          "bg-gray-100 text-gray-700"
                        }`}>
                          {req.confirmationStatus || "Pending"}
                        </span>
                      </td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </div>
        </div>

        {/* AI INFO */}
        <div className="bg-green-50 border-l-4 border-green-600 rounded-xl p-6 shadow-lg">
          <AlertCircle className="w-6 h-6 text-green-600" />
          <p className="text-green-800 font-semibold mt-2">
            AI is now using REAL active donors for matching.
          </p>
        </div>
      </div>

      <AIChatbot />
    </main>
  );
}

/* ----------------------------------------------
   STAT CARD COMPONENT
-----------------------------------------------*/
function StatCard({ icon: Icon, label, value }: any) {
  return (
    <div className="bg-linear-to-r from-red-500 to-red-700 text-white rounded-xl shadow-lg p-6">
      <div className="flex justify-between items-center mb-2">
        <h3 className="text-sm font-semibold">{label}</h3>
        <Icon className="w-6 h-6 opacity-80" />
      </div>
      <p className="text-3xl font-bold">{value}</p>
    </div>
  );
}
